.Page-A-propos p{
    font-size: 25px;
    padding-bottom: 10%;
    text-align: center;
    color: black;
    text-shadow: 0.1em 0.1em 0.1em #bbc0ba;
}
.A-propos-title{
    font-size: 45px;
    margin: auto;
    padding-top: 8%;
    padding-bottom: 10%;
    margin-top: 5%;
    margin-bottom: 15%;
    color: rgb(71, 119, 8);
    text-align: center;
    box-shadow: 1px 1px 10px 5px #f7d411 inset ;
    text-shadow: 0.1em 0.1em 0.1em rgb(199, 194, 194);
    animation: myAnim 2s ease 0s 1 normal forwards;
}
@keyframes myAnim {

    0%,
    100% {
        transform: translateX(0%);
        transform-origin: 50% 50%;
    }

    15% {
        transform: translateX(-30px) rotate(6deg);
    }

    30% {
        transform: translateX(15px) rotate(-6deg);
    }

    45% {
        transform: translateX(-15px) rotate(3.6deg);
    }

    60% {
        transform: translateX(9px) rotate(-2.4deg);
    }

    75% {
        transform: translateX(-6px) rotate(1.2deg);
    }
}